import keyboardPackage from '../../components/keyboard-package/keyboard-package.vue';
import passwordInput from '../../components/password-input/password-input.vue';
export default {
    data() {
        return {
            nickname: '',
            address: '',
            password: '',
            id: '',
            user_id: '',
            numberList: [],
            length: 6,
            type: 'number',
            passIn: false,
            delShow: false
        };
    },
    components: {
        keyboardPackage,
        passwordInput
    },
    onLoad(options) {
        this.nickname = options.wallet_key;
        this.address = options.wallet_value;
        this.id = options.id;
        this.user_id = options.user_id;
    },
    onBackPress(option) {
        plus.key.hideSoftKeybord();
    },
	computed: {
		allowLogin () {
			return !!(this.address && this.nickname)
		},
	},
    methods: {
        //返回
        back: function() {
            uni.navigateBack({
                delta: 1
            });
        },
        clo: function() {
            this.passIn = false;
            this.$refs['number'].close();
            this.numberList.length = 0;
        },
        cloo: function() {
            this.delShow = false;
            this.$refs['numbers'].close();
            this.numberList.length = 0;
        },
        onChange(e) {
            if (e.show == false) {
                this.passIn = false;
            }
            this.numberList.length = 0;
        },
        onChange1(e) {
            if (e.show == false) {
                this.delShow = false;
            }
            this.numberList.length = 0;
        },
        onDelete() {
            this.numberList.pop();
        },
        onConfirm() {
            if (this.numberList.length != 6) {
                uni.showToast({
                    title: '请输入六位交易密码！',
                    duration: 2000,
                    icon: 'none'
                });
            }
        },
        getAddress: function(e) {
            this.address = e.detail.value;
        },
        getRemark: function(e) {
            this.nickname = e.detail.value;
        },
        save: function() {
            if (this.address == '') {
                uni.showToast({
                    title: '请输入我的地址',
                    icon: 'none',
                    duration: 2000
                });
                return false;
            }
            if (this.address.length <= 32) {
                uni.showToast({
                    title: '我的地址最少32位',
                    icon: 'none',
                    duration: 2000
                });
                return false;
            }
            if (this.nickname == '') {
                uni.showToast({
                    title: '请输入地址备注',
                    icon: 'none',
                    duration: 2000
                });
                return false;
            }
            this.passIn = true;
            this.$refs['number'].open();
            this.onInput(val);
        },
        onInput(val) {
            var that = this;
            that.numberList.push(val);
            that.password = that.numberList.join().replace(/,/g, '');
            if (that.numberList.length >= that.length) {
                uni.request({
                    url: that.url + 'updatadeleteaddresss/', //编辑地址接口
                    method: 'PUT',
                    data: {
                        wallet_value: that.address,
                        wallet_key: that.nickname,
                        id: that.id,
                        password: that.password,
                        user_id: that.user_id
                    },
                    header: {
                        Authorization: 'JWT' + ' ' + this.global_.token
                    },
                    success(res) {
                        if (res.statusCode == 400) {
                            that.numberList.pop();
                            that.numberList.length = 0;
                            that.$refs.wrong.flag = false;
                            var n = res.data.data.err_num;
                            var s = 5 - n;
                            that.$refs.wrong.tip = '密码错误，剩余' + s + '次机会';
                        }
                        if (res.statusCode == 423) {
                            that.passIn = false;
                            that.$refs['number'].close();
                            uni.showToast({
                                title: '交易密码已锁定,请在今日24:00后进行交易',
                                icon: 'none',
                                duration: 2000
                            });
                        }
                        if (res.statusCode == 204) {
                            that.passIn = false;
                            that.$refs['number'].close();
                            uni.navigateBack({
                                delta: 1
                            });
                            uni.showToast({
                                title: '修改成功',
                                icon: 'none',
                                duration: 1500
                            });
                        }
                        var page = getCurrentPages().pop();
                        if (page == undefined || page == null) return;
                        page.onLoad();
                    }
                });
                this.numberList.length = 0;
            }
        },
        //点击删除按钮
        onNavigationBarButtonTap: function() {
            var that = this;
            this.delShow = true;
            this.$refs['numbers'].open();
            this.onInput1(val);
        },
        onInput1(val) {
            var that = this;
            that.numberList.push(val);
            that.password = that.numberList.join().replace(/,/g, '');
            if (that.numberList.length >= that.length) {
                uni.request({
                    url: that.url + 'updatadeleteaddresss/', //删除地址接口
                    method: 'DELETE',
                    data: {
                        id: this.id,
                        password: this.password
                    },
                    header: {
                        Authorization: 'JWT' + ' ' + this.global_.token
                    },
                    success(res) {
                        if (res.statusCode == 204) {
                            that.passIn = false;
                            that.$refs['number'].close();
                            uni.navigateBack({
                                delta: 1
                            });
                            uni.showToast({
                                title: '删除成功',
                                icon: 'none',
                                duration: 2000
                            });
                        }
                        if (res.statusCode == 400) {
                            that.numberList.pop();
                            that.numberList.length = 0;
                            that.$refs.wrong.flag = false;
                            var n = res.data.data.err_num;
                            var s = 5 - n;
                            that.$refs.wrong.tip = '剩余' + s + '次机会';
                        }
                        if (res.statusCode == 423) {
                            that.passIn = false;
                            that.$refs['number'].close();
                            uni.showToast({
                                title: '交易密码已锁定,请在今日24:00后进行交易',
                                icon: 'none',
                                duration: 2000
                            });
                        }
                        var page = getCurrentPages().pop();
                        if (page == undefined || page == null) return;
                        page.onLoad();
                    }
                });
                this.numberList.length = 0;
            }
        }
    }
};
